.cardWrapper {
  width: 20%;
  display: flex;
  justify-content: center;
  margin-bottom: 50px;

  .card {
    width: 90%;
    position: relative;
    border: 2px solid rgba(255, 255, 255, 0.15);

    .new {
      position: absolute;
      font-family: sans-serif;
      left: 0;
      right: 0;
      padding: 3px;
      text-align: center;
      top: 0;
      background: #FFC124;
    }

    .action {
      position: absolute;
      font-family: sans-serif;
      left: 0;
      right: 0;
      padding: 3px;
      text-align: center;
      top: 0;
      background: #FF0000;
    }

    .img {
      background-size: cover;
      background-position: 0 0;
      height: 200px;
    }

    .cardContent {
      padding: 40px 10px 10px 10px;
      font-size: 18px;
      font-family: sans-serif;
      background: #000105;
      position: relative;
      height: 420px;

      .common {
        position: relative;
        top: 50px;
      }

      .name {
        position: absolute;
        top: 10px;
        line-height: 24px;
      }

      .starsAndOther {
        position: relative;
        display: flex;
        top: 50px;

        img {
          display: inline-block;
          margin: 0 1px;
        }

        .compare {
          margin-left: 22px;
        }

        .basket {
          width: 30px;
          margin-left: 5px;
          cursor: pointer;
        }
      }

      .comment {
        color: #7072FF;
        margin-top: 10px;
      }

      .have {
        color: #17B978;
        margin-top: 10px;
      }

      .havent {
        color: #FF0000;
        margin-top: 10px;
      }

      .producer {
        position: relative;
        top: 40px;
        margin-top: 20px;
      }

      .buy {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        bottom: 10px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .wantToBuy {
          display: none;
        }

        .price {
          position: absolute;
          top: -45%;
          font-size: 40px;
          color: #17B978;

          span {
            font-size: 18px;
            color: #fff;
            margin-left: 2px;
          }
        }

        .buyButton1 {
          font-size: 25px;
          margin-bottom: 10px;
        }

        .buyButton2 {
          display: none;
        }
        
        .moreDetailButton {
          a {
            color: #c5c5c5;
            background: #5352ff;
            transition: 0.2s linear;
            
            &:hover {
              color: #fff;
            }
          }
        }
      }
    }
  }

  .tabsWrapper {
    display: none;
  }
}