.tableWrapper {
  display: flex;
  justify-content: center;

  .table {

    tr {
      background: rgba(0, 0, 0, 0.4);

      &:first-child {
        font-weight: 600;
        background: rgba(0, 0, 0, 0.6);
      }

      td {
        font-size: 18px;
        font-family: sans-serif;
        border: 1px solid #fff;
        padding: 8px 10px;
      }
    }
  }
}

