.section {
  height: 150px;
  width: 20%;
  background: #000105;
  border: 1px solid rgba(255, 255, 255, 0.15);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0 50px 20px 50px;
  text-align: center;
  position: relative;

  img {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 20px;
  }
}

.active {
  background: #100f34;
}