.button {
  border: 2px solid transparent;
  background: #FFC124;
  color: #000;
  height: 45px;
  width: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  transition: 0.1s linear;

  &:hover {
    border: 2px solid #fff;
    color: #fff;
    background: #db9d24;
  }
}