.searchField {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
  align-items: center;

  p {
    white-space: nowrap;
    font-size: 18px;
    font-family: sans-serif;
  }

  input {
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 2px;
    color: #fff;
    margin: 0 15px;
    padding: 8px 8px;
    outline: none;
    font-size: 18px;
    letter-spacing: 1.5px;
  }
  
  .button {
    background: rgb(224, 165, 37);
    height: 39px;
    width: 23%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
    border: 2px dashed transparent;
    font-size: 18px;
    font-family: sans-serif;
    transition: 0.1s linear;

    &:hover {
      border: 2px dashed white;
    }
  }
}