.menu {
  display: flex;

  li {
    position: relative;
    margin-right: 45px;
  }

  .active {
    color: gold;
    position: relative;
    transition: 0.1s linear;

    &:after {
      content: '';
      display: block;
      position: absolute;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background: gold;
      transition: 0.3s linear;
    }
  }
}