.app {
  background-size: cover;
  background-position: 0 0;
  position: relative;
  z-index: 1;

  .eclipse {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.6);
  }

  .appContent {
    position: relative;
    z-index: 3;
  }
}