*,
::after,
::before {
    box-sizing: border-box;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1400px;
}

img {
    display: block;
    max-width: 100%;
}

body {
    background: #6d6e69;
    color: #fff;
    font-size: 14px;
    font-family: sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
    display: block;
}