.cardWrapper {
  width: 100%;
  padding-bottom: 50px;

  .card {
    display: flex;
    position: relative;
    border: 2px solid rgba(230, 230, 230, 0.8);

    .new {
      position: absolute;
      font-family: sans-serif;
      left: 0;
      right: 0;
      padding: 5px;
      text-align: center;
      top: 0;
      background: #FFC124;
    }

    .action {
      position: absolute;
      font-family: sans-serif;
      left: 0;
      right: 0;
      padding: 3px;
      text-align: center;
      top: 0;
      background: #FF0000;
    }

    .img {
      position: relative;
      background-size: cover;
      background-position: 0 0;
      width: 33%;
    }

    .cardContent {
      padding: 40px 40px;
      font-size: 18px;
      font-family: sans-serif;
      background: #000105;
      position: relative;
      height: 330px;
      width: 67%;

      .common {
        position: relative;
        top: 50px;
        margin-top: 30px;
      }

      .name {
        position: absolute;
        top: 20px;
        max-width: 500px;
        font-size: 24px;
        line-height: 30px;
      }

      .starsAndOther {
        position: absolute;
        right: 50px;
        top: 20px;
        img {
          width: 35px;
          display: inline-block;
          margin: 0 1px;
        }

        .compare {
          position: absolute;
          top: 0;
          left: -30%;
        }
      }

      .comment {
        color: #7072FF;
        margin-top: 20px;
        position: absolute;
        right: 70px;
        font-size: 20px;
      }

      .have {
        color: #17B978;
        font-size: 20px;
      }

      .havent {
        color: #FF0000;
        font-size: 20px;
      }

      .producer {
        position: relative;
        top: 40px;
        margin-top: 30px;
        font-size: 20px;
      }

      .id {
        margin-top: 10px;
        font-size: 20px;
      }

      .buy {
        position: absolute;
        right: 80px;
        bottom: 40px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .wantToBuy {
          display: block;
          position: absolute;
          top: -265%;
          font-size: 40px;

          span {
            font-size: 50px;
            transition: 0.2s linear;
            -webkit-user-select: none;
            -ms-user-select: none;
            user-select: none;

            &:first-child {
              cursor: pointer;
              position: relative;
              width: 40px;
              height: 40px;
              display: inline-block;
              border: 1px solid transparent;
              margin-right: 10px;

              &:hover {
                border: 2px dashed #ff4a4a;
                border-radius: 50%;

                &:after {
                  background: #ff4a4a;
                }
              }

              &:after {
                transition: 0.2s linear;
                content: '';
                width: 25px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%);
                height: 3px;
                background: #fff;
                display: block;
              }
            }

            &:last-child {
              cursor: pointer;
              position: relative;
              width: 40px;
              height: 40px;
              display: inline-block;
              border: 1px solid transparent;
              margin-left: 10px;

              &:hover {
                border: 2px dashed green;
                border-radius: 50%;

                &:after {
                  background: green;
                }

                &:before {
                  background: green;
                }
              }

              &:after {
                transition: 0.2s linear;
                content: '';
                width: 25px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%);
                height: 3px;
                background: #fff;
                display: block;
              }

              &:before {
                transition: 0.2s linear;
                content: '';
                width: 25px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%) rotate(90deg);
                height: 3px;
                background: #fff;
                display: block;
              }
            }
          }
        }

        .price {
          position: absolute;
          top: -100%;
          font-size: 40px;
          color: #17B978;

          span {
            font-size: 18px;
            color: #fff;
            margin-left: 2px;
          }
        }

        .buyButton1 {
          display: none;
        }

        .buyButton2 {
          font-size: 25px;
          bottom: 30px;
           a {
             background: #fff;
           }
        }

        .moreDetailButton {
          display: none;
        }
      }
    }
  }

  .tabsWrapper {
    display: block;
    border: 2px solid rgba(230, 230, 230, 0.8);
    background: green;

    .tabs {
      display: flex;

      div {
        cursor: pointer;
        font-family: sans-serif;
        font-size: 18px;
        padding: 20px;
        background: green;
        transition: 0.2s linear;
      }

      .active {
        background: #005900;
        transition: 0.2s linear;
      }
    }

    .content {
      padding: 20px;
      height: 50px;
      font-family: sans-serif;
      background: #005900;
    }
  }
}


